* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Space Grotesk', 'Inter', 'Segoe UI', system-ui, sans-serif;
  line-height: 1.6;
  color: #000000;
  background: #FFE951;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  background: #FFE951;
  position: relative;
}

/* Navigation */
.navbar {
  background: #FF6B9D;
  color: #000000;
  padding: 1.5rem 0;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  border-bottom: 5px solid #000000;
  box-shadow: 8px 8px 0px #000000;
}

.navbar nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.navbar h1 {
  font-size: 2rem;
  font-weight: 900;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: -0.05em;
}

.navbar ul {
  display: flex;
  list-style: none;
  gap: 1rem;
}

.navbar button {
  background: #FFE951;
  border: 4px solid #000000;
  color: #000000;
  cursor: pointer;
  padding: 0.75rem 1.5rem;
  border-radius: 0;
  transition: all 0.1s ease;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
  box-shadow: 4px 4px 0px #000000;
}

.navbar button:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000000;
}

.navbar button:active {
  transform: translate(4px, 4px);
  box-shadow: 0px 0px 0px #000000;
}

/* Main content */
main {
  margin-top: 100px;
}

/* Sections */
.section {
  padding: 5rem 0;
  min-height: auto;
  position: relative;
}

.section:nth-child(1) {
  background: #FFE951;
}

.section:nth-child(2) {
  background: #B4F8C8;
}

.section:nth-child(3) {
  background: #A0C4FF;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section h2 {
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
  margin-bottom: 4rem;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: -0.05em;
  text-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
}

/* About Me */
.about-content p {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.7;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  color: #000000;
  background: #FFFFFF;
  padding: 2rem;
  border: 5px solid #000000;
  box-shadow: 10px 10px 0px #000000;
}

/* Qualifications */
.qualifications-content {
  display: grid;
  gap: 4rem;
}

.skills-section h3,
.experience-section h3,
.certifications-section h3 {
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 2rem;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
}

.skill-tag {
  background: #FFA6F6;
  color: #000000;
  border: 4px solid #000000;
  padding: 1rem 1.5rem;
  border-radius: 0;
  font-size: 1rem;
  font-weight: 800;
  transition: all 0.1s ease;
  box-shadow: 6px 6px 0px #000000;
  text-transform: uppercase;
}

.skill-tag:hover {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #000000;
}

.certifications-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
  margin-bottom: 1rem;
}

.cert-tag {
  background: #FFD93D;
  color: #000000;
  border: 4px solid #000000;
  padding: 0.75rem 1.25rem;
  border-radius: 0;
  font-size: 0.9rem;
  font-weight: 700;
  text-align: center;
  transition: all 0.1s ease;
  box-shadow: 5px 5px 0px #000000;
}

.cert-tag:hover {
  transform: translate(2px, 2px);
  box-shadow: 3px 3px 0px #000000;
}

.experience-list {
  display: grid;
  gap: 2.5rem;
}

.experience-item {
  background: #FFFFFF;
  padding: 2.5rem;
  border-radius: 0;
  border: 5px solid #000000;
  transition: all 0.1s ease;
  box-shadow: 12px 12px 0px #000000;
}

.experience-item:hover {
  transform: translate(4px, 4px);
  box-shadow: 8px 8px 0px #000000;
}

.experience-item h4 {
  font-size: 1.8rem;
  font-weight: 900;
  color: #000000;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.company {
  color: #000000;
  font-weight: 700;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

/* Contact */
.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

.contact-info h3 {
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 1.5rem;
  color: #000000;
  text-transform: uppercase;
}

.contact-info p {
  font-size: 1.2rem;
  line-height: 1.7;
  margin-bottom: 2rem;
  color: #000000;
  font-weight: 700;
}

.contact-details {
  display: grid;
  gap: 1rem;
}

.contact-item {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #000000;
  font-weight: 700;
  background: #FFFFFF;
  padding: 1rem;
  border: 4px solid #000000;
  box-shadow: 5px 5px 0px #000000;
}

.contact-item a {
  color: #0066FF;
  text-decoration: none;
  font-weight: 900;
  transition: color 0.1s ease;
}

.contact-item a:hover {
  color: #FF006E;
  text-decoration: underline;
  text-decoration-thickness: 3px;
}

/* Contact Form */
.contact-form {
  background: #FFA6F6;
  padding: 2.5rem;
  border-radius: 0;
  border: 5px solid #000000;
  box-shadow: 12px 12px 0px #000000;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 900;
  color: #000000;
  font-size: 1.1rem;
  text-transform: uppercase;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1rem;
  border: 4px solid #000000;
  border-radius: 0;
  font-size: 1rem;
  background: #FFFFFF;
  color: #000000;
  transition: all 0.1s ease;
  font-weight: 600;
  box-shadow: 4px 4px 0px #000000;
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: #666666;
  font-weight: 600;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #000000;
  background: #FFFFCC;
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000000;
}

.submit-btn {
  background: #00F5FF;
  color: #000000;
  padding: 1.25rem 2rem;
  border: 5px solid #000000;
  border-radius: 0;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 900;
  transition: all 0.1s ease;
  width: 100%;
  text-transform: uppercase;
  box-shadow: 8px 8px 0px #000000;
}

.submit-btn:hover {
  transform: translate(4px, 4px);
  box-shadow: 4px 4px 0px #000000;
}

.submit-btn:active {
  transform: translate(8px, 8px);
  box-shadow: 0px 0px 0px #000000;
}

/* Add description styling */
.experience-item .description {
  color: #000000;
  line-height: 1.7;
  font-weight: 600;
}

/* Responsive */
@media (max-width: 768px) {
  .navbar nav {
    flex-direction: column;
    gap: 1rem;
    padding: 0 1rem;
  }

  .navbar h1 {
    font-size: 1.5rem;
  }

  .navbar ul {
    gap: 0.5rem;
    flex-wrap: wrap;
    justify-content: center;
  }

  .navbar button {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
  }

  .contact-content {
    grid-template-columns: 1fr;
  }

  .skills-grid,
  .certifications-grid {
    justify-content: center;
  }

  .section h2 {
    font-size: 2.5rem;
  }

  .section {
    padding: 3rem 0;
  }

  .container {
    padding: 0 1rem;
  }

  main {
    margin-top: 160px;
  }

  .about-content p {
    font-size: 1.1rem;
    padding: 1.5rem;
  }

  .experience-item h4 {
    font-size: 1.4rem;
  }

  .contact-info h3,
  .skills-section h3,
  .experience-section h3,
  .certifications-section h3 {
    font-size: 2rem;
  }
}
